
































































































import { Vue, Component } from 'vue-property-decorator';

import { EDatetimeFormat, ETableRowType, ETableSorting } from '@/../enums';
import { ServiceDatetime, ServiceServerConfig } from '@/../helpers';
import { IComponent } from '@/../models';

import Loading from '@/../components/loading/loading.vue';

import WebXTable from '@/../components/table/table.vue';

import ToolboxMoreV1 from '@/../components/toolbox/more.vue';
import ToolboxMoreCreate from '@/../components/toolbox/toolbox-more-create.vue';
import ToolboxMoreEdit from '@/../components/toolbox/toolbox-more-edit.vue';
import ToolboxMorePreview from '@/../components/toolbox/toolbox-more-preview.vue';
import ToolboxMoreDelete from '@/../components/toolbox/toolbox-more-delete.vue';

import ToolboxCreateItem from '@/../components/toolbox/toolbox-create-item.vue';
import ToolboxEditItem from '@/../components/toolbox/toolbox-edit-item.vue';
import ToolboxPreviewItem from '@/../components/toolbox/toolbox-preview-item.vue';
import ToolboxDeleteItem from '@/../components/toolbox/toolbox-delete-item.vue';

interface ITableApiParam {
    paging: {
        page: number;
        pageSize: number;
    };
    sorting?: {
        field?: string;
        order?: number;
    };
}

interface ITableData {
    objectId: string;
    username: string;
    role: string;
    name: string;
    email: string;
    showCheckbox: boolean;
}

@Component({
    components: {
        ToolboxMoreV1,
        ToolboxMoreCreate,
        ToolboxMoreEdit,
        ToolboxMorePreview,
        ToolboxMoreDelete,
        ToolboxCreateItem,
        ToolboxEditItem,
        ToolboxPreviewItem,
        ToolboxDeleteItem,
        WebXTable,
    },
})
export default class VuePageClass extends Vue {
    private tableItem: IComponent.ITableItem = {
        path: '/user/web',
        showPaging: true,
        showCheckbox: true,
        checkboxDisabled: false,
        datas: [],
        columns: [],
        paging: {
            pageSize: 10,
            page: 1,
            totalPages: 0,
            total: 0,
        },

        actions: {
            disableEdit: false,
            disablePerview: false,
            disableDelete: false,
        },

        // Option: only default sorting
        sorting: {
            field: undefined,
            orderEnum: ETableSorting.desc,
            order: undefined,
        },

        // // Option: change conditions
        // conditions: [5, 10, 25, 50, 100],
        // condition: 50,
    };

    private tableParam: IComponent.ITableParam = {
        page: 1,
        pageSize: 10,
        sortingField: undefined,
        sortingOrderEnum: ETableSorting.none,
    };

    private tableApiParam: ITableApiParam = {
        paging: {
            page: 1,
            pageSize: 10,
        },
        sorting: {},
    };

    //////////////////// variable ////////////////////

    private loading = new Loading();

    //////////////////// Vue Life ////////////////////

    private async beforeCreate() {}
    private async created() {
        this.tableInit();
    }
    private async beforeMount() {}
    private async mounted() {}
    private async beforeDestroy() {}
    private async destroyed() {}

    private async tableInit() {
        this.tableItem.columns = [
            { title: this.$i18n.Common_NO, type: ETableRowType.autoIndex },
            { title: this.$i18n.Common_User_Name, key: 'name', sort: true },
            { title: this.$i18n.Common_Account, key: 'username', information: `login use-test` },
            { title: this.$i18n.Common_Role, key: 'role', sort: true },
            { title: this.$i18n.Common_Email, key: 'email', type: ETableRowType.any },
        ];

        let tableParam: IComponent.ITableParam = {
            page: this.tableItem.paging.page,
            pageSize: this.tableItem.paging.pageSize,
            sortingField: this.tableItem.sorting && this.tableItem.sorting.field ? this.tableItem.sorting.field : '',
            sortingOrderEnum: this.tableItem.sorting && this.tableItem.sorting.orderEnum ? this.tableItem.sorting.orderEnum : ETableSorting.none,
            sortingOrder: this.tableItem.sorting && this.tableItem.sorting.order != undefined ? this.tableItem.sorting.order : undefined,
        };
        this.tableReload(tableParam);
    }

    private async tableReload(tableParam: IComponent.ITableParam) {
        if (tableParam && tableParam.page && tableParam.pageSize) {
            this.tableItem.paging.page = tableParam.page ? tableParam.page : this.tableItem.paging.page;
            this.tableItem.paging.pageSize = tableParam.pageSize ? tableParam.pageSize : this.tableItem.paging.pageSize;

            this.tableApiParam.paging = {
                page: this.tableItem.paging.page,
                pageSize: this.tableItem.paging.pageSize,
            };
        }

        if (tableParam && tableParam.sortingField && tableParam.sortingOrder !== undefined) {
            this.tableApiParam.sorting = {
                field: tableParam.sortingField,
                order: tableParam.sortingOrder,
            };
        } else {
            delete this.tableApiParam.sorting;
        }

        this.loading.show();

        let data = this.getFakeData(tableParam);

        this.tableSetData(data);

        this.loading.hide();
    }

    private getFakeData(tableParam: IComponent.ITableParam): any {
        let data: any[] = JSON.parse(JSON.stringify(PERSONS));

        // sorting
        if (!!tableParam && !!tableParam.sortingField && tableParam.sortingOrderEnum !== ETableSorting.none) {
            let sortingField: string = tableParam.sortingField;
            data = data.sort((a, b) => {
                if (tableParam.sortingOrder === 0) {
                    if (typeof a[sortingField] === 'number') {
                        return a[sortingField] - b[sortingField];
                    }

                    a[sortingField] = a[sortingField] || '';
                    b[sortingField] = b[sortingField] || '';
                    if (a[sortingField] > b[sortingField]) return 1;
                    if (a[sortingField] < b[sortingField]) return -1;
                } else {
                    if (typeof a[sortingField] === 'number') {
                        return b[sortingField] - a[sortingField];
                    }

                    a[sortingField] = a[sortingField] || '';
                    b[sortingField] = b[sortingField] || '';
                    if (a[sortingField] > b[sortingField]) return -1;
                    if (a[sortingField] < b[sortingField]) return 1;
                }

                return 0;
            });
        }

        // pagination
        let pageSize: number = tableParam?.pageSize || this.tableApiParam.paging.pageSize;
        let total: number = data.length;
        let totalPages: number = Math.ceil(total / pageSize);
        let page: number = tableParam?.page || this.tableApiParam.paging.page;
        if (page > totalPages) {
            page = totalPages || 1;
        }

        this.tableItem.paging.pageSize = pageSize;
        this.tableItem.paging.page = page;
        this.tableItem.paging.totalPages = totalPages;
        this.tableItem.paging.total = total;

        data = data.slice((page - 1) * pageSize, page * pageSize);

        return data;
    }

    private tableSetData(result: any) {
        let tableData: ITableData[] = [];

        (result || []).forEach((element) => {
            tableData.push({
                objectId: element.objectId ? element.objectId : '',
                username: element.username,
                role: element.role,
                name: element.name,
                email: element.email,
                showCheckbox: element.showCheckbox,
            });
        });

        this.tableItem.datas = tableData;
    }

    //////////////////// Form Show ////////////////////

    private async tableCreate(row: any) {
        console.log('!!! tableCreate', row);
    }

    private async tableEdit(row: any) {
        console.log('!!! tableEdit', row);
    }

    private async tablePreview(row: any) {
        console.log('!!! tablePreview', row);
    }

    private async tableDelete(row: any) {
        console.log('!!! tableDelete', row);
    }

    private async tableClickCheckbox(selectData: any[]) {
        // console.log('!!! tableClickCheckbox', selectData);
    }

    private hasAdditional(): boolean {
        return this.tableItem.datas.length < 0;
    }

    //////////////////// Toolbox ////////////////////
    private clickCreate(row: object): void {
        console.log('!!! clickCreate', row);
    }
    private clickEdit(row: object): void {
        console.log('!!! clickEdit', row);
    }
    private clickPreview(row: object): void {
        console.log('!!! clickPreview', row);
    }
    private clickDelete(row: object): void {
        console.log('!!! clickDelete', row);
    }
}

