

















































































































































































































































































































































import { Vue, Component, Prop, Watch } from 'vue-property-decorator';

import { ETableSorting, ETableRowType } from '@/../enums';
import { IComponent } from '@/../models';

import ImageView from '@/../components/image/image_view.vue';

@Component({
    components: {
        ImageView,
    },
})
export default class Table extends Vue {
    //#region Prop
    @Prop({
        type: Array,
        default: () => {
            return [];
        },
    })
    private datas: IComponent.ITableColumn[];

    @Prop({
        type: Boolean,
        default: () => {
            return true;
        },
    })
    private showPaging: boolean;

    @Prop({
        type: Array,
        default: () => {
            return [];
        },
    })
    private value: any[];

    @Prop({
        type: Object,
        default: () => {
            return {
                pageSize: 10,
                page: 1,
                totalPages: 0,
                total: 0,
            };
        },
    })
    private paging: IComponent.ITablePaging;

    @Prop({
        type: Array,
        default: () => {
            return [];
        },
    })
    private columns: IComponent.ITableColumn[];

    @Prop({
        type: Object,
        default: () => {
            return {
                disableDelete: false,
                disableEdit: false,
                disablePerview: false,
            };
        },
    })
    private actions: IComponent.ITableActions;

    @Prop({
        type: Object,
        default: () => {
            return {
                field: undefined,
                orderEnum: ETableSorting.none,
            };
        },
    })
    private sorting: IComponent.ITableSorting;

    @Prop({
        type: Number,
        default: () => {
            return 10;
        },
    })
    private condition: number;

    @Prop({
        type: Array,
        default: () => {
            return [5, 10, 25, 50, 100];
        },
    })
    private conditions: number[];

    @Prop({
        type: Boolean,
        default: () => true,
    })
    private showCheckbox: boolean;

    @Prop({
        type: Boolean,
        default: () => false,
    })
    private checkboxDisabled: boolean;
    //#endregion

    //#region Variable
    private selectItem = {
        datas: [],
        all: false,
    };

    pageItem = {
        eTableRowType: ETableRowType,
    };

    private param: IComponent.ITableParam = {
        page: 1,
        pageSize: 10,
        sortingField: undefined,
        sortingOrderEnum: ETableSorting.none,
    };

    private checkboxStatusList: boolean[] = [];
    private thAllSelected: boolean = false;
    //#endregion

    //#region Watch
    @Watch('value', { immediate: true, deep: true })
    private valueChange(newVal: any[], oldVal: any[]): void {
        this.selectItem.all = false;
        for (let tempIndex in newVal) {
            this.checkboxStatusList[tempIndex] = false;
        }

        this.$emit('input', this.value);
    }

    @Watch('paging', { immediate: true, deep: true })
    private pagingChange(newVal: IComponent.ITablePaging, oldVal: IComponent.ITablePaging): void {
        this.paging = newVal;
        this.param.page = newVal.page;
        if (newVal != this.paging) {
            // this.paging = newVal;
            // this.param.page = newVal.page;
            this.reloadTable();
        }
    }

    @Watch('columns', { immediate: true, deep: true })
    private columnsChange(newVal: IComponent.ITableColumn[], oldVal: IComponent.ITableColumn[]): void {
        if (newVal != this.columns) {
            this.columns = newVal;
        }
    }

    @Watch('actions', { immediate: true, deep: true })
    private actionsChange(newVal: IComponent.ITableActions, oldVal: IComponent.ITableActions): void {
        if (newVal != this.actions) {
            this.actions = newVal;
        }
    }

    @Watch('sorting', { immediate: true, deep: true })
    private sortingChange(newVal: IComponent.ITableSorting, oldVal: IComponent.ITableSorting): void {
        this.param.sortingField = newVal.field;
        this.param.sortingOrderEnum = newVal.orderEnum;
        if (newVal.order != undefined) {
            this.param.sortingOrder = newVal.order;
        } else {
            delete this.param.sortingOrder;
        }
    }

    @Watch('condition', { immediate: true })
    private conditionChange(newVal: number, oldVal: number): void {
        if (newVal != this.paging.pageSize) {
            this.param.pageSize = newVal;
        }
    }
    //#endregion

    //#region Vue Life
    private beforeCreate(): void {}
    private created(): void {
        this.initParam();
    }
    private beforeMount(): void {}
    private mounted(): void {}
    private beforeDestroy(): void {}
    private destroyed(): void {}
    //#endregion

    //#region Init Data
    private initParam(): void {
        this.param.page = this.paging.page;
        this.param.pageSize = this.paging.pageSize;
        this.param.sortingField = this.sorting.field;
        this.param.sortingOrderEnum = this.sorting.orderEnum;
        if (this.param.sortingOrderEnum == ETableSorting.asc) {
            this.param.sortingOrder = 0;
        }
        if (this.param.sortingOrderEnum == ETableSorting.desc) {
            this.param.sortingOrder = 1;
        }
    }
    //#endregion

    //#region Update table and page
    private async reloadTable() {
        this.$emit('table-reload', this.param);
    }

    private async updatePageSize() {
        this.param.page = 1;
        this.reloadTable();
    }

    private updateAssignPage(): void {
        let page: number = undefined;

        if (Number(this.param.page) < 1) {
            page = 1;
        } else if (Number(this.param.page) > this.paging.totalPages) {
            page = this.paging.totalPages;
        } else {
            page = Number(this.param.page);
        }

        this.param.page = page;
        this.reloadTable();
    }

    private resolveAutoIndex(index: number): string {
        if (!isNaN(index)) {
            return (this.paging.pageSize * (this.paging.page - 1) + (index + 1)).toString();
        }
        return '';
    }
    //#endregion

    //#region Sorting
    private sortClick(event: any, index: any, row: any): boolean {
        let thIconRef: any = this.$refs[`table-th-icon-${index}`];
        if (!row.sort) {
            return false;
        }
        if (thIconRef && thIconRef[0] && thIconRef[0].classList) {
            let haveSort = false;
            let haveSortAsc = false;
            let haveSortDesc = false;
            for (let cal of thIconRef[0].classList) {
                if (cal == 'fa-sort') {
                    haveSort = true;
                    break;
                }
                if (cal == 'fa-sort-asc') {
                    haveSortAsc = true;
                    break;
                }
                if (cal == 'fa-sort-desc') {
                    haveSortDesc = true;
                    break;
                }
            }

            for (let tempIndex in this.columns) {
                let tempRef = this.$refs[`table-th-icon-${tempIndex}`];
                if (tempRef) {
                    tempRef[0].classList.remove('fa-sort-asc');
                    tempRef[0].classList.remove('fa-sort-desc');
                    tempRef[0].classList.add('fa-sort');
                }
            }
            if (haveSort) {
                thIconRef[0].classList.remove('fa-sort');
                thIconRef[0].classList.add('fa-sort-asc');
                this.param.sortingField = row.key;
                this.param.sortingOrderEnum = ETableSorting.asc;
                this.param.sortingOrder = 0;
                this.reloadTable();
            }
            if (haveSortAsc) {
                thIconRef[0].classList.remove('fa-sort');
                thIconRef[0].classList.add('fa-sort-desc');
                this.param.sortingField = row.key;
                this.param.sortingOrderEnum = ETableSorting.desc;
                this.param.sortingOrder = 1;
                this.reloadTable();
            }
            if (haveSortDesc) {
                this.param.sortingField = '';
                this.param.sortingOrderEnum = ETableSorting.none;
                delete this.param.sortingOrder;
                this.reloadTable();
            }
        }
    }

    private showSorting(item: any): boolean {
        return item.sort && item.key != '';
    }

    private showSortingNone(item: any): boolean {
        return !this.showSortingAsc(item) && !this.showSortingDesc(item);
    }

    private showSortingAsc(item: any): boolean {
        return this.sorting.field == item.key && this.sorting.orderEnum == ETableSorting.asc;
    }

    private showSortingDesc(item: any): boolean {
        return this.sorting.field == item.key && this.sorting.orderEnum == ETableSorting.desc;
    }
    //#endregion

    //#region Action
    private showActions(): boolean {
        let result = true;
        if (this.actions.disableDelete && this.actions.disableEdit && this.actions.disablePerview) {
            result = false;
        }
        return result;
    }

    private clickCreate(data: any): void {
        console.log('create', data);
    }
    private clickEdit(data: any): void {
        console.log('edit', data);
    }
    private clickPreview(data: any): void {
        console.log('preview', data);
    }
    private clickDelete(data: any): void {
        console.log('delete', data);
    }

    private showRowEdit(row: any): boolean {
        if (this.actions.disableEdit) {
            return false;
        }
        if (row.actions && row.actions.disableEdit === true) {
            return false;
        }
        return true;
    }

    private showRowPreview(row: any): boolean {
        if (this.actions.disablePerview) {
            return false;
        }
        if (row.actions && row.actions.disablePerview === true) {
            return false;
        }
        return true;
    }

    private showRowDelete(row: any): boolean {
        if (this.actions.disableDelete) {
            return false;
        }
        if (row.actions && row.actions.disableDelete === true) {
            return false;
        }
        return true;
    }

    private createRow(row: any) {
        this.$emit('table-create', row);
    }

    private editRow(row: any) {
        this.$emit('table-edit', row);
    }

    private previewRow(row: any) {
        this.$emit('table-preview', row);
    }

    private deleteRow(row: any) {
        this.$emit('table-delete', row);
    }
    //#endregion

    // #region Checkbox
    private checkboxSelectItem(event: any, index: number) {
        this.selectItem.datas = [];
        this.checkboxStatusList[index] = !this.checkboxStatusList[index];
        for (let i in this.checkboxStatusList) {
            let tempCheckbox = this.checkboxStatusList[i];

            if (tempCheckbox && this.value[i] != undefined) {
                this.selectItem.datas.push(this.value[i]);
            }
        }

        if (this.checkSelectAll()) {
            this.selectItem.all = true;
        } else {
            this.selectItem.all = false;
        }

        this.selectItem.datas = this.selectItem.datas.filter((x) => !(x.showCheckbox === false));

        this.$emit('table-click-checkbox', JSON.parse(JSON.stringify(this.selectItem.datas)));
    }

    private checkboxSelectAll() {
        this.selectItem.datas = [];

        for (let index in this.checkboxStatusList) {
            this.checkboxStatusList[index] = true;

            if (this.value[index]?.showCheckbox === false) {
            } else {
                this.selectItem.datas.push(this.value[index]);
            }

            // this.selectItem.datas = this.value;
            if (this.selectItem.all) {
                this.checkboxStatusList[index] = !this.checkboxStatusList[index];
                this.selectItem.datas = [];
            }
        }

        // remove array's element undefined / null / empty
        this.selectItem.datas = this.selectItem.datas.filter((x) => x);

        this.$emit('table-click-checkbox', JSON.parse(JSON.stringify(this.selectItem.datas)));
    }

    private checkSelectNotSelect(): boolean {
        return this.selectItem.datas.length === 0;
    }

    private checkSelectNotAll(): boolean {
        return this.selectItem.datas.length !== this.value.length;
    }

    private checkSelectAll(): boolean {
        return this.selectItem.datas.length === this.value.length;
    }

    private isShowCheckboxIcon(isShow: boolean): boolean {
        return !(isShow === false);
    }

    //#endregion

    //#region Paging
    // private showPaging(): boolean {
    //     return true;
    //     // return this.paging.total > this.paging.pageSize;
    // }

    private disabledFirst(): boolean {
        return this.paging.page == 1;
    }

    private disabledLast(): boolean {
        return this.paging.page == this.paging.totalPages;
    }

    private changePage(page: number) {
        if (!isNaN(page)) {
            this.param.page = page;
            this.reloadTable();
        }
    }

    private showPrev(index: number): boolean {
        let result = false;
        let page = this.paging.page;
        let totalPages = this.paging.totalPages;
        switch (index) {
            case 1:
                if (page > 1) {
                    result = true;
                }
                break;
            case 2:
                if (page > 2) {
                    result = true;
                }
                break;
            case 3:
                if (page > 3 && totalPages < page + 2) {
                    result = true;
                }
                break;
            case 4:
                if (page > 4 && totalPages < page + 1) {
                    result = true;
                }
                break;
        }
        return result;
    }

    private showNext(index: number): boolean {
        let result = false;
        let page = this.paging.page;
        let totalPages = this.paging.totalPages;
        switch (index) {
            case 1:
                if (totalPages > page) {
                    result = true;
                }
                break;
            case 2:
                if (totalPages > page + 1) {
                    result = true;
                }
                break;
            case 3:
                if (totalPages > page + 2 && page < 3) {
                    result = true;
                }
                break;
            case 4:
                if (totalPages > page + 3 && page < 2) {
                    result = true;
                }
                break;
        }
        return result;
    }
    //#endregion

    //#region ToolTip
    private showToolTip(tip: string): string {
        let result: string = '';
        const N = '\r\n';

        if (tip) {
            const tips: string[] = tip.split('-');

            (tips || []).forEach((tip) => (result += `<div style="float:left">${tip}</div> <br>`));
        }

        return result;
    }
    //#endregion
}
Vue.component('web-x-table', Table);
